import React, { JSX } from 'react';
import { IconSizeEnum } from '../icons/Icon';
import DiamondControl from './DiamondControl';
import useBuySmth from '../../hooks/useBuySmth';
import { useAppDispatch, useAppSelector } from '../../hooks/redux';
import { AppDispatch, RootState } from '../../store/store';
import { decreaseDiamonds } from '../../store/emoji-mapper/emojiMapper.slice';

interface BuyForDiamondsControlProps {
	price: number;
	className?: string;
}

function BuyForDiamondsControl({ price, ...props }: BuyForDiamondsControlProps): JSX.Element {
	const diamondsCount: number = useAppSelector((state: RootState) => state.emojiMapperReducer.diamondsCount);
	const dispatch: AppDispatch = useAppDispatch();

	const buyHandler = (): void => {
		console.log('buyHandler')
		if (diamondsCount >= price) {
			dispatch(decreaseDiamonds(price));
		}
	};

	return (
		<DiamondControl onClick={buyHandler} {...props} size={IconSizeEnum.S} count={price}/>
	);
}

export default BuyForDiamondsControl;